import * as React from 'react';
import { DataGrid } from '@mui/x-data-grid';
import * as d3 from 'd3';
import * as myData from '/Users/jonahmiller/Documents/ncaa-viz/src/current data.csv';
import { makeStyles } from "@material-ui/core";


function createData(id, date, favteam, undteam, favspread, predoutcome, confidence, vig, overunder, predtotal, overundervig,overunderbet,overunderconf, bet_to_take, half_kelly, quarter_kelly, overunderhkelly, finalscore, cover, overcover) {
  // console.log(date, favteam, undteam, favspread, predoutcome, confidence, vig, bet_to_take, finalscore, cover);
  return { id, date, favteam, undteam, favspread, predoutcome, confidence, vig,  overunder, predtotal, overundervig,overunderbet,overunderconf, bet_to_take,  half_kelly, quarter_kelly, overunderhkelly, finalscore, cover, overcover };
}

const columns = [
  // { wordBreak: 'break-word'},
  { headerName: "Spread Model"},
  {'whiteSpace': 'unset'},
  // {style: { 'whiteSpace': 'unset' }},
  { field: 'id', headerName: 'ID', flex:1 , hide:true},
  { field: 'date', headerName: 'Date', flex:1},
  { field: 'favteam', headerName: 'Favored Team',  width:150, headerClassName: "wrapHeader" },
  { field: 'undteam', headerName: 'Underdog Team',  width:150, headerClassName: "wrapHeader" },
  { field: 'favspread', headerName: 'Favored Spread',  flex:1,headerClassName: "wrapHeader"  },
  { field: 'predoutcome', headerName: 'Predicted Outcome', width: 250 },
  { field: 'confidence', headerName: 'Spread Confidence', flex:1,width: 100 },
  { field: 'vig', headerName: 'Vig', width: 60, hide:true},
  { field: 'overunder', headerName: 'Over Under', width: 100, hide:true},
  { field: 'predtotal', headerName: 'Predicted Total', width: 100, hide:true},
  { field: 'overundervig', headerName: 'Over Under Vig', width: 130,hide:true },
  { field: 'overunderbet', headerName: 'Over Under Bet', width: 130,hide:true },
  { field: 'overunderconf', headerName: 'Over Under Confidence', width: 130,hide:true },
  { field: 'bet_to_take', headerName: 'Recommended Bet', width: 200 },
  { field: 'half_kelly', headerName: 'Half Kelly',  flex:1  },
  { field: 'quarter_kelly', headerName: 'Quarter Kelly',  flex:1,hide:true  },
  { field: 'overunderhkelly', headerName: 'Over Under Half Kelly',  flex:1,hide:true },
  { field: 'finalscore', headerName: 'Final Score',  flex:1 },
  { field: 'cover', headerName: 'Spread Cover',  flex:1 },
  { field: 'overcover', headerName: 'Over Under Cover',  flex:1,hide:true },

];


const useStyle = makeStyles({
  root: {
    "& .wrapHeader .MuiDataGrid-colCellTitle": {
      overflow: "visible",
      lineHeight: "1.43rem",
      whiteSpace: "normal"
    }
  }
});

export default function DataTable() {
    const classes = useStyle();
    const [data, setData] = React.useState([]);
    var rows =  [];
  
    React.useEffect(() => {
      d3.csv(myData).then(setData);
    }, []);  
  
    var iterations = Object.values(data).length - 1;
    for (let i = 0; i < iterations; i++) {
      var valsToParse = Object.values(data)[i]
      const ta = []
      // getting row id
      ta.push(i);
      for (let j = 1; j < 20; j++) {
        var key = Object.keys(valsToParse)[j]
        ta.push(valsToParse[key]);
  
      }
      var row_data = [];
  
      // console.log(ta)
      row_data = createData(ta[0],ta[1],ta[2],ta[3],ta[4],ta[5],ta[6],ta[7],ta[8],ta[9], ta[10], ta[11], ta[12], ta[13],  ta[14], ta[15], ta[16], ta[17], ta[18], ta[19]);
      rows.push(row_data);
    }    


  return (
    <div style={{ height: 1000, width: '100%' }}>
      <DataGrid
        
        className={classes.root}
        headerHeight={75}
        rows={rows}
        columns={columns}
        pageSize={20}
        rowsPerPageOptions={[20]}
        // options={options}
        
        checkboxSelection
      />
    </div>
  );
}